.cookie-dialog {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 9999;
  }
  
  .cookie-dialog-text {
    font-size: 14px;
  }
  
  .cookie-dialog-buttons {
    display: flex;
    align-items: center;
  }
  
  .cookie-dialog-button {
    background-color: white;
    color: black;
    border: none;
    padding: 10px 20px;
    font-size: 14px;
    margin: 0 5px;
    border-radius: 10px;
    cursor: pointer;
    &:hover{
        background: #000;
        color: #fff;
    }
  }
@media (max-width:720px){
    .cookie-dialog {
    flex-direction: column;
    z-index: 9999;
  }
   .cookie-dialog-text {
    margin-bottom: 10px;
  }
}
@media (max-width:1200px){
    .cookie-dialog {
    flex-direction: column;
    z-index: 9999;
  }
   .cookie-dialog-text {
    margin-bottom: 10px;
  }
}