.wrapper{
   
    height: 30px;
  
}
.inner {
    z-index: 999;
    position: absolute;
    height: 30px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 0 150px;
    margin-right: auto;
    margin-left: auto;
    position: fixed;
    background-color: #000;
    color: #fff;
    
}
.header-language{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
}
.header-langue-item{
    margin-right: 20px;
    font-weight: 400;
}
@media (max-width:1080px){
    .inner{
        padding: 0px;
    }
    .header-language{
        display: none;
    }
    .header-contact-info{
        text-align: center;
        width: 100%;
    }
}