@import url(https://fonts.googleapis.com/css?family=Quattrocento+Sans);

@mixin position-center($text-align: center) {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	text-align: $text-align;
}

.loading {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 9999;
	
}

.loading-text {
	@include position-center;
	width: 100%;
	height: 100px;
	
	line-height: 100px;
	
	span {
		display: inline-block;
		margin: 0 5px;
		color: #000;
		font-size: 2rem;
        font-family: 'Quattrocento Sans', sans-serif;
		@for $i from 0 through 18 {
			&:nth-child(#{$i + 1}) {
				filter: blur(0px);
				animation: blur-text 1.5s (#{$i/5})+s infinite linear alternate;
			}
		}
	}
}

@keyframes blur-text {
	0% {filter: blur(0px);}
	100% {filter: blur(4px);}
}
