.wrapper {
    padding: 0 150px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    margin-top: 20px;
}
.inner {
    display: flex;
    align-items: center;
}
.ban-cursor {
    cursor:not-allowed;
    opacity: 0.5;
  }
.back-button {
    font-size: 2rem;
    padding: 10px 15px;
    background-color: transparent;
    font-weight: 600;
    color: var(--primary);
    display: inline-flex;
    align-items: center;
    border-radius: 2px;

    .icon {
        margin-right: 5px;
        font-size: 1.5rem;
    }
    &:hover {
        color: #000;
        transform: scale(1.03);
    }
}
.mobile-info {
    display: none;
}
.pc-info {
    display: block;
}
.infomation {
    margin-top: 5px;
    width: 100%;
    h1 {
        color: rgb(72, 71, 71);
    }
    h3 {
        color: var(--primary);
        margin: 10px 0;
    }
    p {
        font-size: 2.5rem;
        margin-top: 30px;
    }
    .button-customer {
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;
        align-items: center;
        margin-top: 20px;
    }

    .icon-like {
        display: inline-block;
        margin-left: 20px;
        border-radius: 100%;
        align-items: center;
        justify-content: center;
        position: relative;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        
        &:hover {
            color: #000;
            padding: 0px;
            margin-left: 20px;

            cursor: pointer;
            width: 40px;
            height: 40px;
            margin-bottom: 0;
        }
        &:hover::before {
            opacity: 1;
            transform: scale(1.3);
            top: 0px;
            right: 7px;
        }
        &::before {
            content: "";
            position: absolute;
            z-index: -1;
            top: -5px;
            right: 0;

            width: 100%;
            height: 100%;
            border-radius: 50%;
            background: #fff;
            transform: scale(0.1);
            opacity: 0;
            box-shadow: 0px 5px 10px 0px rgb(76 70 124 / 50%);
            transition: all 0.4s cubic-bezier(0.35, 2.57, 0.13, 0.88);
        }
    }
    .icon-like:active .icon-mui {
        transform: scale(1.5);
        color: #000;
      }
    .icon-customer {
        font-size: 30px;
        display: inline-block;
        width: 20%;
    }
    button {
        font-size: 14px;
        padding: 10px 50px;
        width: 50%;
        background-color: #000;
        color: #fff;
        border-radius: 5px;
        cursor: pointer;
        margin-right: 10px;
        margin-top: 0px;
        text-transform: uppercase;
        font-weight: 500;
    }
}
.product-image {
    width: 350px;
        border-radius: 30px;
    margin-right: 10px;
}
.product-title {
    width: 100%;
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 10px;
}
.option-extra {
    display: flex;
    flex-wrap: wrap;
    
    .product-item {
        padding: 10px 15px;
        width: 45px;
        cursor: pointer;
        margin-right: 8px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 15px;
        border-radius: 50%;
        background-color: #fff;
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px -8px rgba(0, 0, 0, 0.4);
        font-weight: 600;
    }
    .product-mood {
        width: 50%;
        .mood-body {
            display: flex;
            align-items: center;
            &:nth-child(1) {
                color: rgb(94, 0, 0);
            }
        }
    }
    .product-sugar {
        width: 50%;
        .sugar-body {
            display: flex;
            align-items: center;
        }
    }
    .product-ice {
        width: 50%;
        .ice-body {
            display: flex;
            align-items: center;
        }
    }
}
.same-product {
    display: flex;
    justify-content: space-around;
    margin: 40px 0;
    img {
        width: 150px;
    }
    h2 {
        font-size: 1.5rem;
        font-weight: 500;
        text-align: center;
        color: #000;
    }
    .same-product-item {
        cursor: pointer;
        padding: 20px;
        transform: translateY(0);
        &:hover {
            transform: translateY(-5px) scale(1.03);
            box-shadow: rgb(153 153 153 / 60%) 0px 0px 10px 0px;
        }
    }
}

.popup-content {
    width: 50%;
    h1 {
        font-size: 2.5rem;
        font-weight: 600;
    }
    h3 {
        font-size: 1.3rem;
        font-weight: 400;
    }
    input {
        font-size: 1.5rem;
        padding: 20px 20px 10px 0;
        margin-bottom: 20px;
        width: 100%;
        &:hover {
            border-bottom: 1px solid #ccc;
        }
        &:focus {
            border-bottom: 1px solid #ccc;
        }
    }
    button {
        width: 100%;
        padding: 20px;
        font-size: 1.5rem;
        border-radius: 10px;
        margin-left: -5px;
        cursor: pointer;
        &:hover {
            background-color: #000;
            color: #fff;
        }
    }
}

.popup-qr {
    width: 50%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    .qr {
        width: 50%;
        display: flex;
        justify-content: center;
    }

    .bank {
        margin-bottom: 20px;
        h1 {
            font-size: 2rem;
            color: rgb(116, 116, 116);
        }
        p {
            font-weight: 500;
            color: var(--primary);
        }
    }
    .momo {
        h1 {
            font-size: 2rem;
            color: rgb(116, 116, 116);
        }
        p {
            font-weight: 500;
            color: var(--primary);
        }
    }
}
.wrapper-loading {
    text-align: center;
}
.center {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    span {
        color: #737373;
        font-size: 20px;
        text-transform: uppercase;
        letter-spacing: 1px;
        line-height: 200px;
        color: #000;
        animation: text 3s ease-in-out linear;
    }
}
@keyframes text {
    50% {
        color: white;
    }
}

.fly-to-cart {
    animation: FlyToCart 0.6s linear;
}
@keyframes FlyToCart {
    0% {
        transform: scale(0.4);
    }
    10% {
        position: relative;
        top: -60px;
        right: -120px;
        transform: scale(0.3);
    }
    20% {
        position: relative;
        top: -100px;
        right: -220px;
        transform: scale(0.2);
    }
    30% {
        position: relative;
        top: -140px;
        right: -320px;
        transform: scale(0.1);
    }
    40% {
        position: relative;
        top: -180px;
        right: -420px;
        transform: scale(0.1);
    }
    50% {
        position: relative;
        top: -220px;
        right: -520px;
        transform: scale(0.1);
    }
    60% {
        position: relative;
        top: -260px;
        right: -620px;
        transform: scale(0.1);
    }
    70% {
        position: relative;
        top: -300px;
        right: -720px;
        transform: scale(0.1);
    }

    100% {
        position: relative;
        top: -380px;
        right: -1020px;
        transform: scale(0.1);
    }
}
.ring {
    position: absolute;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    animation: ring 2s linear infinite;
    &::before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        border-radius: 50%;
        box-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
    }
}
@keyframes ring {
    0% {
        transform: rotate(0deg);
        box-shadow: 1px 5px 2px #000;
    }
    50% {
        transform: rotate(180deg);
        box-shadow: 1px 5px 2px rgb(82, 82, 82);
    }
    100% {
        transform: rotate(360deg);
        box-shadow: 1px 5px 2px rgb(129, 129, 129);
    }
}

.wrapper-error {
    align-items: center;
    margin-top: 50px;
    display: inline-flex;
    justify-content: center;
    width: 100%;
}
.error {
    display: flex;
    align-items: center;
    color: #fff;
    background-color: rgb(251, 109, 109);
    padding: 20px 30px;
    border-radius: 10px;
    border: 3px solid red;
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;
    .icon {
        margin-right: 10px;
    }
}

select {
    width: 80px;
    border-radius: 10px;
    font-size: 1.5rem;
    padding: 5px 20px;
    font-weight: 600;
    height: 30px;
    option {
        text-align: left;
    }
}
.quantity-product {
    margin: 15px 10px;

    h2 {
        font-size: 14px;

        font-weight: 600;
        margin-right: 10px;
    }
    display: flex;
    align-items: center;
    flex-direction: column;
}
.back-button-mobile {
    display: none;
}
.mobile-price {
    display: none;
}
.pc-price {
    display: block;
    .pc-price-topping{
        font-size: 1rem;
        letter-spacing: 0.1rem;
        span{
            margin-left: 3px;
        }
    }
}
.active {
    color: #fff;
    background-color: #000 !important;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
}
.css-1nmdiq5-menu {
    z-index: 150;
}
