$white: rgba(255, 255, 255, 0.3);

.register-body {
  background: url("../../assets/footerbackground.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  font-family: "Montserrat", sans-serif;
}

.container {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 60%;
  left: 50%;
  margin-top: 40px;
}
.registerImage {
  display: flex;
  max-width: 300px;
  margin: auto;
}
.image-user-avatar {
  width: 50px;
  border-radius: 50%;
  height: 50px;
  object-fit: cover;
  margin-right: 10px;
}
.form-register {
  background: $white;
  padding: 3em;
  height: auto;
  border-radius: 10px;
  border-left: 1px solid $white;
  border-top: 1px solid $white;
  backdrop-filter: blur(6px);
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
  text-align: center;
  position: relative;
  transition: all 0.2s ease-in-out;
  min-width: 500px;
  max-width: 988px;

  p {
    font-weight: 500;
    color: #000;
    opacity: 0.9;
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 60px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }

  a {
    text-decoration: none;
    color: #000;
    font-size: 1.8rem;
    float: right;

    &:hover {
      text-shadow: 2px 2px 6px #00000040;
    }

    &:active {
      text-shadow: none;
    }
  }

  input {
    background: transparent;
    width: 300px;
    padding: 1em;
    margin-bottom: 2em;
    border: none;
    border-left: 1px solid $white;
    border-top: 1px solid $white;
    border-radius: 5000px;
    backdrop-filter: blur(5px);
    box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
    color: #000;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    }

    &[type="email"],
    &[type="password"] {
      &:focus {
        background: rgba(255, 255, 255, 0.1);
        box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
      }
    }

    &[type="button"] {
      margin-top: 10px;
      width: 180px;
      font-size: 1.5rem;

      &:hover {
        cursor: pointer;
      }

      &:active {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }

  &:hover {
    margin: 4px;
  }
}

::placeholder {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  color: #000;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
}

a,
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
.password-input-btn-register {
  position: relative;
  input {
    width: 300px;
  }
  .button-show-password {
    position: absolute;
    top: 13px;
    right: 60px;
    z-index: 1;
    backdrop-filter: blur(5px);
    font-size: 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    background: #e0e0e0;
    color: #787878;
  }
}
.input-type-password {
  position: relative;
}
.password-strength-bar {
  position: absolute !important;
  top: 45px;
  right: 60px;
  width: 70%;
  text-align: center;
}
.signup-btn {
  margin-top: 10px;
  width: 180px;
  font-size: 1.5rem;
  background: transparent;
  width: 300px;
  padding: 1em;
  margin-bottom: 2em;
  border: none;
  border-left: 1px solid $white;
  border-top: 1px solid $white;
  border-radius: 5000px;
  backdrop-filter: blur(5px);
  box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
  color: #000;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  &:hover {
    cursor: pointer;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    transform: scale(1.02);
  }

  &:active {
    background: rgba(255, 255, 255, 0.2);
  }
}
@media (max-width: 728px) {
  .register-body {
    height: 140vh;
  }
  .form-register {
    background: $white;
    padding: 2em 1em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid $white;
    border-top: 1px solid $white;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: fit-content;
    max-width: 988px;
    input {
      background: transparent;
      width: 300px;
      padding: 1em;
      min-height: 42px;
      margin-bottom: 2em;
      border: none;
      border-left: 1px solid $white;
      border-top: 1px solid $white;
      border-radius: 5000px;
      backdrop-filter: blur(5px);
      box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
      color: #000;
      font-family: Montserrat, sans-serif;
      font-weight: 500;
      transition: all 0.2s ease-in-out;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    }
  }
  .password-input-btn-login {
    position: relative;
    input {
      width: 300px;
    }
    button {
      position: absolute;
      top: 12px;
      right: 20px;
      z-index: 1;
      backdrop-filter: blur(5px);
      font-size: 1.5rem;
      border-radius: 50%;
      cursor: pointer;
      background: #e0e0e0;
      color: #787878;
    }
  }
}
