$white: rgba(255, 255, 255, 0.3);

.login-body {
  background: url("../../assets/footerbackground.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  font-family: "Montserrat", sans-serif;
}
.loading-overlay{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  .load{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);
    /*change these sizes to fit into your project*/
    width:100px;
    height:100px;
  }
  .load hr{border:0;margin:0;width:40%;height:40%;position:absolute;border-radius:50%;animation:spin 2s ease infinite}
  
  .load :first-child{background:#000;animation-delay:-1.5s}
  .load :nth-child(2){background:rgb(131, 131, 131);animation-delay:-1s}
  .load :nth-child(3){background:rgb(78, 77, 77);animation-delay:-0.5s}
  .load :last-child{background:rgb(255, 255, 255)}
  
  @keyframes spin{
    0%,100%{transform:translate(0)}
    25%{transform:translate(160%)}
    50%{transform:translate(160%, 160%)}
    75%{transform:translate(0, 160%)}
  }

}
.container {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 60%;
  left: 50%;
}

.form-login {
  background: $white;
  padding: 3em;
  height: auto;
  border-radius: 10px;
  border-left: 1px solid $white;
  border-top: 1px solid $white;
  backdrop-filter: blur(6px);
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
  text-align: center;
  position: relative;
  transition: all 0.2s ease-in-out;
  min-width: 500px;
  max-width: 988px;

  p {
    font-weight: 500;
    color: #000;
    opacity: 0.9;
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 60px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }
  .form-footer {
    display: flex;
    flex-direction: column;
    margin-right: 50px;
    align-items: flex-end;
    .forgot-btn {
      font-size: 1.5rem !important;
      margin-bottom: 30px;
    }
  }
  a {
    text-decoration: none;
    font-size: 1.8rem;
    float: right;
    color: #000;

    &:hover {
      text-shadow: 2px 2px 6px #00000040;
    }

    &:active {
      text-shadow: none;
    }
  }

  input {
    background: transparent;
    width: 300px;
    padding: 1em;
    margin-bottom: 2em;
    border: none;
    border-left: 1px solid $white;
    border-top: 1px solid $white;
    border-radius: 5000px;
    backdrop-filter: blur(5px);
    box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
    color: #000;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    }

    &[type="email"],
    &[type="password"] {
      &:focus {
        background: rgba(255, 255, 255, 0.1);
        box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
      }
    }

    &[type="button"] {
      margin-top: 10px;
      width: 180px;
      font-size: 1.5rem;

      &:hover {
        cursor: pointer;
      }

      &:active {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }

  &:hover {
    margin: 4px;
  }
}
.login-btn {
  margin-top: 10px;
  width: 180px;
  font-size: 1.5rem;
  background: transparent;
  width: 300px;
  padding: 1em;
  margin-bottom: 2em;
  border: none;
  border-left: 1px solid $white;
  border-top: 1px solid $white;
  border-radius: 5000px;
  backdrop-filter: blur(5px);
  box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
  color: #000;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  &:hover {
    cursor: pointer;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    transform: scale(1.02);
  }

  &:active {
    background: rgba(255, 255, 255, 0.2);
  }
}

::placeholder {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  color: #000;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
}

a,
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

.password-input-btn-login {
  position: relative;
  input {
    width: 300px;
  }
  div {
    position: absolute;
    top: 12px;
    right: 60px;
    z-index: 1;
    backdrop-filter: blur(5px);
    font-size: 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    background: #e0e0e0;
    color: #787878;
  }
}
