.wrapper{
    padding: 0 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: auto;
}
.header-verify{
    margin: 30px 0;
    .header-icon{
        font-size: 13rem;
    }
}
.body-verify{
    margin: 30px 0;
   .body-head{
        font-size: 5rem;
        letter-spacing: 1rem;
   }
   .body-body{
        margin: 40px 0;
        font-size: 2rem;
        letter-spacing: 0.5rem;
        align-items: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .body-button{
        padding: 20px 50px;
        font-size: 1.5rem;
        letter-spacing: 0.3rem;
        border-radius: 10px;
        background-color: #000;
        color: #fff;
        box-shadow: 0 10px 0 rgb(49, 49, 49);
        display: flex;
        align-items: center;
        margin: auto;
        justify-content: center;
        margin-top: 50px;
        &:active{
            opacity: 0.9;
            box-shadow:none;
            transform: translateY(10px);
        }
    }
}
@media (max-width:1200px){
    .wrapper{
        padding: 0 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: auto;
        .body-head{
            font-size: 3rem;
            letter-spacing: 1rem;
            text-align: center;
       }
       .body-body{
        margin: 40px 0;
        font-size: 2rem;
        letter-spacing: 0.5rem;
        align-items: center;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
    }
    } 
}