.grid{
    width: 100%;
    max-width: 100%;
    /* margin: 0 20px; */
  
}
  
.grid_full-width{
    width: 100%;
}
  
.grid_row{
    display: flex;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
    margin-bottom: 20px;
    margin: 0 auto;
    max-width: 1140px;
    justify-content: space-between;
    align-items: center;
}
.grid_column-2{
    padding-left: 5px;
    padding-right: 5px;
    width: 16.66667%;
    flex: 1;
}
.grid__column-2-4{
    padding-left: 5px;
    padding-right: 5px;
    width: 25%; 
}
.grid_column-50{
    width: 50%;
}
.grid__column-10{
    padding-left: 5px;
    padding-right: 5px;
    width: 83,3333%;
    flex: 5;
}
.container {
    background: #fff;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
    padding: 100px 50px ;
    position: sticky;
}
.footer_logo-img{
    width: 150px;
    height: 150px;
    background: url(../../../assets/logo/tachnen/greylogo.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
}
.footer-description{
    padding: 10px 0;
    color: #000;
    font-size: 1.5rem;
    .footer-social{
        padding: 10px ;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
   
        .social-icon-footer{
            font-size: 2rem;
         
            padding:  15px;
            border-radius: 50%;
            backdrop-filter: blur(6px);
            box-shadow: 20px 20px 40px -5px rgba(0,0,0,0.2);
            
            
        }
        
        .social-content{
           display: none;
        }
        
    }
}
.footer-title{
    color: #000;
    font-weight: 600;
    font-size: 2rem;

}

.footer-about{
    display: flex;
    flex-direction: row;
    margin-left: -15px;
}


.footer-copyright-2{
  text-align: center;
  margin:20px 0 ;
  padding: 10px 0;
  color: rgb(111, 111, 111);
  display: flex;
  justify-content: space-around;
  font-size: 15px;
  a{
    color: #000;
    font-weight: 600;
    &:hover{
        opacity: 0.8;
    }
  }
}
@media (max-width:728px){
    .grid_row{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .footer-about{
        display: flex;
        flex-direction: row;
        margin-left: 0px;
    }
    .footer-description {
        width: 100%;
        .footer-social{
            padding: 10px ;
            display: flex;

            justify-content: flex-start;
            align-items: center;
            position: relative;
       
            .social-icon-footer{
                font-size: 2rem;
             
                padding:  15px;
                border-radius: 50%;
                backdrop-filter: blur(6px);
                box-shadow: 20px 20px 40px -5px rgba(0,0,0,0.2);
               
                
            }
            
            .social-content{
               display: none;
            }
            
        }
    }
    .grid__column-2-4{
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .footer-copyright-2{
        flex-direction: column;
        align-items: flex-start;
    }
}