.form-input{
    margin: 10px 15px;
}
.body-profile{
    width: 80%;
    text-align: center;
    display: flex;
    margin: auto;
    animation: slideUp ease-in 0.8s;
    .button-change{
        padding: 20px 30px;
        border-radius: 20px;
        cursor: pointer;
        &:hover{
            color: #fff;
            background-color: #000;
        }
    }
}

.form-input-center {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
}

.password-input-btn{
    position: relative;
   input{
     width: 300px;
     
   }
   button{
    position: absolute;
    top: 15px;
    right: 20px;
    z-index: 1;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    font-size: 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    background: #ffffff;
    color: #787878;
  
   }
  }

  @keyframes slideUp {
    from{
        opacity: 0;
        transform: translateY(20%);
    }to{
        opacity: 1;
        transform: translateY(0);
    }
}
.input-profile {
    background: transparent;
    width: 300px;
    padding: 1em;
    margin-bottom: 2em;
    border: none;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    border-radius: 5000px;
    backdrop-filter: blur(5px);
    box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
    color: #000;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    }

    &[type="email"],
    &[type="password"] {
      &:focus {
        background: rgba(255, 255, 255, 0.1);
        box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
      }
    }

    &[type="button"] {
      margin-top: 10px;
      width: 180px;
      font-size: 1.5rem;

      &:hover {
        cursor: pointer;
      }

      &:active {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }
@media (max-width: 777px) {
    .body-profile{
        width: 100%;
        text-align: center;
        display: flex;
        margin: auto;
        animation: slideUp ease-in 0.8s;
        height: 150vh;
        .button-change{
            padding: 20px 30px;
            border-radius: 20px;
            cursor: pointer;
            &:hover{
                color: #fff;
                background-color: #000;
            }
        }
    }
    .form-profile{
        background: rgba(255, 255, 255, 0.3);
        padding: 0;
        height: auto;
        border-radius: 10px;
        border-left: 1px solid rgba(255, 255, 255, 0.3);
        border-top: 1px solid rgba(255, 255, 255, 0.3);
        -webkit-backdrop-filter: blur(6px);
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px -6px rgb(0 0 0 / 20%);
        text-align: center;
        position: relative;
        transition: all 0.2s ease-in-out;
        min-width: 100%;
        max-width: 100%;
        
        
    }
    .form-input{
        margin: 10px 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
}