.wrapper{
    padding: 0 150px 30px 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
        width: 500px;
    }
    a{
        background-color: #ccc;
        color: #000;
        padding: 10px 20px;
        border-radius: 20px;
        &:hover{
            background-color: #000;
            color: #fff;
        }
    }  

}