.wrapper{
    background: #fff;
    border-left: 1px solid rgba(255, 255, 255, 0.3);
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgb(0 0 0 / 20%);
    width: 400px;
    border-radius: 15px;
}
.cart-header{
    padding: 10px;
    h1{
        text-align: center;
    }
}

.check-payment{
    margin-left: 10px;
    flex: 1;
    min-height: 100px;
    max-height: 90vh;
    height: 100vh;
    min-width: 265px;
    max-width: 265px;
    overflow: hidden;
    position: fixed;
    z-index: 1;
    right: 65px;
    overflow-x: hidden;
    padding: 8px 0;

    h1{
        font-weight: 500;
        text-align: center;
        border-bottom: 1px solid #ccc;
    }
}
.payment-body{
    margin-top: 10px;
    border-radius: 10px;
    padding:0 20px;
    a{
        color: #000;
    }

}
.payment-item{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    border-radius: 10px;
    justify-content: space-between;
    word-wrap: break-word;

    &:hover{
        box-shadow: rgb(153 153 153 / 60%) 0px 0px 10px 0px;
        transform: scale(1.03);
        
    }
}
.payment-inner{
    display: flex;
    align-items: center;
}
.image{
    width: 80px;
    height: 80px;
    border-radius: 10px;
    object-fit: cover;

}
.payment-info{
    margin: 0 10px;

    & h3 {
        // display: block;
        display: -webkit-box;
        // text-overflow: ellipsis;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

}
.payment-button{
    padding: 5px 10px;
    border-radius: 10px;
    margin-right: 10px;
    cursor: pointer;
    &:hover{
        background-color: #ccc;
    }
}
.payment-total{
    display: flex;
    padding: 0 20px;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    p{
        font-size: 2rem;
        font-weight: 500;
    }
    span,div{
        font-size: 2rem;
        font-weight: 500;
    }
}
.payment-button-check{
    margin-top: 10px;
    width: 100%;
    a{
        color: #fff;
        background-color: rgb(85, 85, 85);
        padding: 10px 20px ;
        border-top-left-radius: 15px ;
        border-bottom-right-radius: 15px;
        width: 100%;
        display: flex;
        justify-content: center;
        &:hover{
            background-color: #000;
        }
    }
    button{
        color: #fff;
        background-color: rgb(85, 85, 85);
        padding: 10px 20px ;
        border-top-left-radius: 15px ;
        border-bottom-right-radius: 15px;
        width: 100%;
        display: flex;
        justify-content: center; 
    }
}
.payment-wrapper{
    scroll-behavior: smooth;
    overflow-y: scroll;
    max-height: 250px;
   
}
.cart-footer{
    text-align: center;
    padding: 20px;
    cursor: pointer;
    font-size: 2rem;
    &:hover{
        transform: scale(1.02);
    }
}