.cart{
    padding: 0 150px;
    a{
        background-color: #000;
        color: #fff;
        padding: 10px 15px;
        font-size: 1.5rem;
        display: inline-flex;
        align-items: center;
        border-radius: 10px;
        margin: 20px 0;
        span{
            margin-left: 5px;
        }
    }
    .cart_item{
        
        .wrapper{
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: #fff;
            
            border-radius: 10px;
            .price-topping{
                font-size: 1rem;
                margin-bottom: 10px;
            }
            &:hover{
                box-shadow: 0 0 10px rgb(0 0 0 / 10%);
                transform: scale(1.02);
            }
        }
        .info{
            color: #000;
        }
    }
}

.topping-product{
    .topping-list{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        max-width: 200px;
        .topping-item{
            margin-top:10px;
            margin-right:20px;
            font-size: 1rem;
        }
    }
}
.inner{
    display: flex;

}
.non-item{
    max-width: 1200px;
    width: 100%;
    padding: 50px;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 600;
    color: var(--primary);
}
.card_summary{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .payment {
        border-radius: 10px;
        padding: 10px 20px;
        font-size: 2rem;
        color: #fff;
        cursor: pointer;
        &:hover{
            color: #fff;
            background-color: rgb(58, 58, 58);
        }

    }
    .back{
        display: none;
    }
}
.card_total{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    
    h3{
        color: var(--primary);
        margin: 5px 0;
    }
}
.popup-content{
    width: 50%;
    h1{
        font-size: 2.5rem;
        font-weight: 600;
    }
    h3{
        font-size: 1.3rem;
        font-weight: 400;
    }
    input{
        font-size: 1.5rem;
        padding:20px 20px 10px 0;
        margin-bottom: 20px;
        width: 100%;
        &:hover{
            border-bottom: 1px solid #ccc;
        }
        &:focus{
            border-bottom: 1px solid #ccc;
        }
    }
    button{
        width: 100%;
        padding: 20px;
        font-size: 1.5rem;
        border-radius: 10px;
        margin-left: -5px;
        cursor: pointer;
        
        }
    
}
.active{

    &:hover{
        background-color: #000;
        color: #fff;
    }
}

.popup-qr{
    width:50%;
    display:flex;
    justify-content: center;   
    flex-direction: column;
    align-items: center;
    .qr{
        width: 50%;
        display: flex;
        justify-content: center;
      
    }
    
    .bank{
        margin-bottom: 20px;
        h1{
            font-size: 2rem;
            color: rgb(116, 116, 116);
        }
        p{
            font-weight: 500;
            color: var(--primary);
        }
    }
    .momo{
        h1{
            font-size: 2rem;
            color: rgb(116, 116, 116);
        }
        p{
            font-weight: 500;
            color: var(--primary);
        }
    }  
}

.image{
    width: 200px;
}
.info{
    flex: 1;
    margin-left: 10px;
    .tile{
        font-size: 1.8rem;
        color: var(--primary);
        margin: 10px 0;
    }
    .price{
    margin: 10px 0;
    }

}
.delete-btn{
    font-size: 2rem;
    padding: 10px 15px;
    cursor: pointer;
    background-color: transparent;
    margin-right: 10px;
    border-radius: 10px;
    &:hover{
        background-color: #ccc;
    }
}
.quantity{
   
    input{
        width: 90px;
        text-align: center;
        border: 1px solid #ccc;
        border-radius: 10px;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 10px;
        
    }
}
.topping-select{
    width: 320px;
    border-radius: 20px;
    .topping-option{
        
    }
}
@media (max-width:728px){
    .cart{
        padding: 10px;
        .back-button-mobile{
            display: inline-block;
            padding: 20px;
            margin-left: 10px;
            border-radius: 50%;
            background: transparent;
            color: #000;
            &:hover{
                transform: scale(1.05);
            }
        }
    }
    .card_summary{
        flex-direction: column;
        text-align: left;
        margin-bottom: 100px;
       
        .button-summary-wrapper{
            display: flex;
            flex: 1;
           
            width: 100%;
            .back-button-mobile{
                display: none;
                
            }
             
        }
        .back{
            display: block;
            flex: 1;
            font-size: 2rem;
            background-color: rgb(67, 67, 67);
        }
        .payment{
            flex: 2;
            margin-left: 5px;
        }
    }
    .image{
        width: 150px;
    }
    .card_total{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
    text-align: left;
    margin-left: 10px;
    
    h3{
        color: var(--primary);
        margin: 5px 0;
    }
}
}