.wrapper {
    background-color: var(--white-color);
    position: relative;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    box-shadow: 0 0.0625rem 0.125rem 0 rgb(0 0 0 / 10%);

    cursor: pointer;
    border-radius: 10px;

    width: calc(100% / 3);
    animation: getIn linear 0.2s;
}
@keyframes getIn {
    from {
        opacity: 0;
        transform: translateY(100%);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
.product-image {
    width: 100%;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    border-radius: 10px;
    margin-bottom: 10px;
    transition: transform 0.25s;
}
.infomation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    h3 {
        font-size: 2.1rem;
        font-weight: 500;
        line-height: 2.4rem;
        margin: 10px 10px 6px;
        height: 2.8rem;
        overflow: hidden;
        color: rgb(80, 80, 80);
        display: block;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
    }
    p {
        display: flex;
        align-items: baseline;
        color: var(--primary);
        font-size: 2.1rem;
        font-weight: 500;
    }
}
.grid__column-menu-3 {
    padding-left: 5px;
    padding-right: 5px;
    position: relative;
    margin-bottom: 45px;
    overflow: hidden;
    margin: 5px 10px;
    border-radius: 10px;
    &:hover {
        box-shadow: rgb(153 153 153 / 60%) 0px 0px 10px 0px;
    }
    &:hover .product-image {
        transform: scale(1.5);
    }
    &:hover .overlay {
        opacity: 1;
    }
    &:hover .info-name-hover {
        
        transform: translateY(-40px);
    }
    &:hover hr {
        width: 75px!important;
      
    }
    &:hover .info-price-hover {
        

        transform: translateY(-40px);
    }
    &:hover .icon-like{
        transform: translateY(10px);
    }
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    transition: all 0.25s;
    opacity: 0;
    z-index: 100;
    .icon-hover {
        .icon-like {
            padding: 5px 10px;
            color: #fff;
            font-size: 2rem;
            transition: all 0.25s;
        }
    }
    .info-hover {
        position: absolute;
        bottom: 24px;
        width: 100%;
        transition: all 0.25s;
        padding: 0 10px;

        hr {
            display: block;
            width: 0;
            border: none;
            border-bottom: solid 3px #fff;
            position: absolute;
            transition: all 0.5s;
            bottom: 100px;
        }
        .info-name-hover {
            color: #fff;
            font-size: 3rem;
            transition: all 0.25s;
            
        }
        .info-price-hover {
            margin: 10px 0;
            color: #fff;
            font-size: 3rem;
            transition: all 0.25s;
            
        }
    }
}
.favorite-icon {
    position: absolute;
    color: #000;
    font-size: 2rem !important;
    top: 10px;
    left: 20px;
}
@media (max-width: 728px) {
    .grid__column-menu-3 {
        padding-left: 5px;
        padding-right: 5px;
        position: relative;
        margin-bottom: 45px;
        overflow: hidden;
        margin: 5px 10px;
        border-radius: 10px;
        &:hover {
            box-shadow: rgb(153 153 153 / 60%) 0px 0px 10px 0px;
        }
        &:hover .product-image {
            transform: scale(1);
        }
    }
    .overlay{
        display: none;
    }
    .wrapper {
        background-color: var(--white-color);
        position: relative;
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
        box-shadow: 0 0.0625rem 0.125rem 0 rgb(0 0 0 / 10%);

        cursor: pointer;
        border-radius: 10px;

        width: calc(100% / 2);
        animation: getIn linear 0.2s;
    }
}
