
.layout-home{
    background: url("../../assets/producthomebackground.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 120vh;
    padding: 0 150px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .none{
        width: 50%;
    }
    .layout-content{
        width: 50%;
        float: right;
        h1{
            font-size: 7rem;
            color: rgb(65, 65, 65);
            letter-spacing: 2px;
        }
        h3{
            margin-left: 100px;
            font-weight: 600;
            font-size: 3rem;
            color: var(--primary);
        }
    }
}
.body{
    padding: 0 150px;
    .inner-body{
    
    padding: 20px;
    }
}
.procedure{
    display: flex;
    align-items: center;
    width: 100%;
    .procedure-content{
        width: 100%;
        h1{
            color: rgb(64, 64, 64);
        }
        p{
            font-weight: 600;
            color: var(--primary);
        }
    }
    .procedure-pic{
        width: 100%;
        .procedure-img{
            width: 100%;
            border-radius: 20px;
        }
    }
}
.product-layout{
    h1{
        text-align: center;
        color: rgb(64, 64, 64);
    }
}
.product-list{
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 20px;
    .product-item{
        img{
            width: 150px;
        }
        h3{
            color: #000 ;
        }
        p{
            color: var(--primary);
            font-size: 1.8rem;
        }
        div{
            color: var(--primary);
            font-size: 1.8rem;
        }
    }
}
@media (max-width: 777px) {
    .layout-home{
        padding: 0;
    }
    .body{
        padding: 0;
    }
}