    }
    @font-face {
        font-family: 'Nunito';
        src: url('../../assets/fonts/Nunito/static/Nunito-Bold.ttf') format('truetype');
        font-weight: 600;
        font-style: normal;
    }
    @font-face {
        font-family: 'Nunito';
        src: url('../../assets/fonts/Nunito/static/Nunito-Medium.ttf') format('truetype');
        font-weight: 500;
