    padding:2em 1em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid $white;
    border-top: 1px solid $white;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: fit-content;
    max-width: 988px;
    input {
      background: transparent;
      width: 300px;
      padding: 1em;
      margin-bottom: 2em;
      border: none;
      border-left: 1px solid $white;
      border-top: 1px solid $white;
      border-radius: 5000px;
      backdrop-filter: blur(5px);
      box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
      color: #000;
      font-family: Montserrat, sans-serif;
      font-weight: 500;
      transition: all 0.2s ease-in-out;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    }
  }
  .password-input-btn-login {
    position: relative;
    input {
      width: 300px;
    }
    button {
      position: absolute;
      top: 12px;
      right: 20px;
      z-index: 1;
      backdrop-filter: blur(5px);
      font-size: 1.5rem;
      border-radius: 50%;
      cursor: pointer;
      background: #e0e0e0;
      color: #787878;
    }
  }
}