.grid__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -5px;
    justify-content: space-between;
    margin-right: -5px;
    width: 100%;
   

}

.wrapper{
    
    padding: 0 150px;
    margin-right: auto;
    margin-left: auto;
    .grid{
        .grid__row{
        }
    }
    
}
.content-body {
    display: flex;
}
.grid{
    background-color: var(--white-color);
    position: relative;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    box-shadow: 0 0.0625rem 0.125rem 0 rgb(0 0 0 / 10%);
    transition: transform linear 0.1s;
    // will-change: transform;
    cursor: pointer;
    border-radius: 10px;
    width: 100%;
    
}
.grid__column-menu-3{
    width: 30%;
}
.grid__column-menu-7{
    width: 70%;
}

.menu-header-title{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    position: fixed;
    z-index: 999;
    height: 100%;
    width: 160px;
    padding-top: 50px;
}
.button-change-type{
    font-size: 2rem;
    background-color: transparent;
    text-transform: uppercase;
    font-weight: 600;
    padding: 5px 0px;
    margin: 0 0px;
    display: block;
    color: rgb(92, 92, 92);
    transform: translateX(0);
    transition: all ease-in .2s;
    cursor: pointer;

    &.active {
        color: black;
        transform: translateX(10px);
        
    }

    &:hover{
        color: #000;
        transform: scale(1.03);
        transform: translateX(10px);

    }
}

@media (min-width:724px) and (max-width: 1185px) {
    .grid__row {
        display: flex;
        flex-wrap: wrap;
        margin-left: -5px;
        justify-content: space-between;
        margin-right: -5px;
        width: 100%;
        margin-bottom: 150px;
    }
    .check-payment{
        display: none;
    
        h1{
            font-weight: 500;
            text-align: center;
            border-bottom: 1px solid #ccc;
        }
    }
    .wrapper{
        padding: 0;
        z-index: -1;
    }
    .grid__row .content-body{
        width: 100%;
        }
}

