
.inner{
    padding: 0 150px;
    margin-top: 10px;
}
.loading-overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    .load{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);
      /*change these sizes to fit into your project*/
      width:100px;
      height:100px;
    }
    .load hr{border:0;margin:0;width:40%;height:40%;position:absolute;border-radius:50%;animation:spin 2s ease infinite}
    
    .load :first-child{background:#000;animation-delay:-1.5s}
    .load :nth-child(2){background:rgb(131, 131, 131);animation-delay:-1s}
    .load :nth-child(3){background:rgb(78, 77, 77);animation-delay:-0.5s}
    .load :last-child{background:rgb(255, 255, 255)}
    
    @keyframes spin{
      0%,100%{transform:translate(0)}
      25%{transform:translate(160%)}
      50%{transform:translate(160%, 160%)}
      75%{transform:translate(0, 160%)}
    }
  
  }
//button 
.fancy {
    background-color: transparent;
    border: 2px solid #000;
    border-radius: 0;
    box-sizing: border-box;
    color: rgb(0, 0, 0);
    cursor: pointer;
    display: inline-block;
    width: 30%;
    text-align: center;
    font-weight: 700;
    letter-spacing: 0.05em;
    margin: 0;
    outline: none;
    overflow: visible;
    padding: 1.25em 2em;
    position: relative;
    text-align: center;
    text-decoration: none;
    text-transform: none;
    transition: all 0.3s ease-in-out;
    user-select: none;
    font-size: 13px;
    min-width: 150px;
   }
   
   .fancy::before {
    content: " ";
    width: 1.5625rem;
    height: 2px;
    background: black;
    top: 50%;
    left: 1.5em;
    position: absolute;
    transform: translateY(-50%);
    transform-origin: center;
    transition: background 0.3s linear, width 0.3s linear;
   }
   
   .fancy .text {
    font-size: 1.125em;
    line-height: 1.33333em;
    padding-left: 2em;
    display: block;
    text-align: left;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
   }
   
   .fancy .top-key {
    height: 2px;
    width: 1.5625rem;
    top: -2px;
    left: 0.625rem;
    position: absolute;
    background: #e8e8e8;
    transition: width 0.5s ease-out, left 0.3s ease-out;
   }
   
   .fancy .bottom-key-1 {
    height: 2px;
    width: 1.5625rem;
    right: 1.875rem;
    bottom: -2px;
    position: absolute;
    background: #e8e8e8;
    transition: width 0.5s ease-out, right 0.3s ease-out;
   }
   
   .fancy .bottom-key-2 {
    height: 2px;
    width: 0.625rem;
    right: 0.625rem;
    bottom: -2px;
    position: absolute;
    background: #e8e8e8;
    transition: width 0.5s ease-out, right 0.3s ease-out;
   }
   
   .fancy:hover {
    color: white;
    background: black;
   }
   
   .fancy:hover::before {
    width: 0.9375rem;
    background: white;
   }
   
   .fancy:hover .text {
    color: white;
    padding-left: 1.5em;
   }
   
   .fancy:hover .top-key {
    left: -2px;
    width: 0px;
   }
   
   .fancy:hover .bottom-key-1,
    .fancy:hover .bottom-key-2 {
    right: 0;
    width: 0;
   }
.cart-empty{
    text-align: center;
    display: flex;
    margin: auto;
    font-size: 2.5rem;
    align-items: center;
    justify-content: center;
}
.payment-information{
    background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: 90%;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
}
.payment-total-wrapper{
    display: flex;
    flex-direction: column;
}
.payment-button-success{
    background: #fff;
    padding: 1em;
    margin-top: 20px;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
}
.momo-button{
    border-radius: 10px;
    padding: 15px 15px;
    background-color: #000;
    color: #fff;
    display: flex;
    height: 46px;
    margin: auto;
    align-items: center;
    cursor: pointer;
    background-color: #a50064;
    .momo-icon{
        width: 30px;
        border-radius: 20px;
    }
    &:hover{
        opacity: 0.8;
    }
}

.payment-body{
    display: flex;
    max-width: 1200px;
    margin-top: 20px;
    .payment-products-list{
        background: #fff;
        padding: 3em;
        height: auto;
        border-radius: 10px;
        border-left: 1px solid #fff;
        border-top: 1px solid #fff;
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
        text-align: center;
        position: relative;
        flex:4;
        transition: all 0.2s ease-in-out;
        margin-right: 20px;
        max-height: 80vh;
        overflow-y: scroll;
        
    }
    .payment-last-total{
        background: #fff;
        padding: 1em 3em;
        height: auto;
        max-height: 30vh;
        border-radius: 10px;
        border-left: 1px solid #fff;
        border-top: 1px solid #fff;
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
        text-align: center;
        position: relative;
        flex:1;
        transition: all 0.2s ease-in-out;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
.payment-product-item-title{
    font-size: 1.8rem;
    font-weight: 600;
    color: rgb(114,114,114);
}
.payment-products-item{
    display: flex;
    align-items: center;
    padding: 20px;
    border-radius: 20px;

    .payment-product-inner-content{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-left: 10px ;
        margin-bottom: 10px;
        .payment-product-item-name{
            
            font-size: 1.8rem;
            width: 150px;
            text-align: left;
            display: flex;
            word-wrap: break-word;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            flex-direction: column;
            span{
                color: #000;
            }
            .topping-list{
                display: flex;
                flex-direction: column;
                max-height: 80px;
                overflow-y: scroll;  
                .topping-item{
                    font-size: 1.2rem;
                    color: #000;
                }
            }
            
        }
       
        .payment-product-item-qty{
            color: #000;
        }
    }
    img{
        width: 8rem;
        border-radius: 10px;
        
    }
}

.payment-wrapper-without-name{
    flex: 1;
    display: flex;
    margin-left: 20px;
    align-items: center;
    justify-content: space-between;
}
.delete-btn{
    font-size: 2rem;
    padding: 10px 15px;
    cursor: pointer;
    background-color: transparent;
    margin-left: 10px;
    border-radius: 10px;
    &:hover{
        background-color: #ccc;
    }
}
.payment-info-content{
    text-align: left;
    margin-left: 10px;
    width: auto;
    display: flex;
    flex-direction: column;
    .payment-title{
        font-size: 1.8rem;
        color: rgb(79, 79, 79);
    }
    span{
        color: rgb(114,114,114);
        
    }
}
.payment-icon{
    width: 80px;
    min-width: 80px;
    height: 80px;
    font-size: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #ccc;
}
.payment-infor-user,.payment-infor-paymentmethod,.payment-infor-address{
    display: flex;
    align-items: center;
}
.payment-product{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px auto;
    
    .payment-title{
        font-size: 1.8rem;
        font-weight: 600;
        color: rgb(114,114,114);
        margin-right: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .payment-ship-distance{
            
            font-size: 1.3rem;
        }
    }
    .payment-price{
        font-size: 1.8rem;
        font-weight: 600;
        color: rgb(114,114,114);
    }
}

@media (max-width:1200px){
    .inner{
        padding: 10px 0 150px 0;
        margin-top: 10px;
        width: 100%;
        
    }
   
    .payment-body{
        display: flex;
        flex-direction: column;
        max-width: 1200px;
        margin-top: 20px;
        .payment-products-list{
            background: #fff;
            padding: 0;
            height: auto;
            border-radius: 10px;
            border-left: 1px solid #fff;
            border-top: 1px solid #fff;
            backdrop-filter: blur(6px);
            box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
            text-align: center;
            position: relative;
            transition: all 0.2s ease-in-out;
            margin-right: 0;
            margin-bottom: 20px;
        
            
            .payment-product-item-single-price{
                display: none;
            }
        }
    }
   
    .payment-information{
        margin-top: 5em;
        background: #fff;
        padding: 1em;
        height: auto;
        border-radius: 10px;
        border-left: 1px solid #fff;
        border-top: 1px solid #fff;
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px 2px rgba(0,0,0,0.2);
        text-align: center;
        position: relative;
        transition: all 0.2s ease-in-out;
        min-width: 100%;
        max-width: fit-content;
        width: 100%;
        display: flex;
        flex-direction:column ;
        justify-content: space-between;
    }
    .payment-infor-user,.payment-infor-paymentmethod,.payment-infor-address{
        display: flex;
        align-items: center;
        margin: 10px 0;
    }
    .payment-products-item{

        .payment-product-inner-content{
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            .payment-product-item-name{
           
                font-size: 2rem;
                
            }
        }
    }
    .payment-wrapper-without-name{
        flex-direction: row;
        text-align: left;
        flex-direction: column;
        margin: 0;
        display: flex;
        width: 100%;
        flex: 1;
        justify-content: flex-start;
        align-items: flex-start;
        .payment-product-item-quanti{
            margin-right: 18%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
        }
        .payment-product-item-title{
            width: 150px;
            text-align: left;
            font-size: 1.5rem;
            
        }
        .payment-product-item-qty{
            width: 100%;
            text-align: left;
            
        }.payment-product-item-price{
            display: none;
        }
    }
    .payment-total-wrapper{

        .payment-button-success{
            a{
                background-color: rgb(86, 86, 86);
            }
            .payment-button{
                margin: 0 5px;
            }
        }
    }
   
}