.inner{
    padding: 0 150px;
    display: flex;
    justify-content: center;
}
.none-product{
  width: 100%;
  text-align: center;
  font-size: 3rem;
  margin: 50px;  
  div{
    margin-bottom: 50px;
  }
  a{
    margin-top: 50px;
    padding: 20px 30px;
    border-radius: 20px;
    background-color: #000;
    color: #fff;
    &:hover{
      background-color: rgb(65, 65, 65);
    }
  }
}
.payment-information{
    background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    flex: 2;
    min-width: 500px;
    max-width: 700px;
   
}
.payment-information-body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px 0;
    text-align: center;

    .input-information {
        background: transparent;
        width: 300px;
        padding: 1em;
        margin-bottom: 2em;
        border: none;
        border-left: 1px solid #fff;
        border-top: 1px solid #fff;
        border-radius: 5000px;
        backdrop-filter: blur(5px);
        box-shadow: 4px 4px 60px rgba(0,0,0,0.2);
        color: #000;
        font-family: Montserrat, sans-serif;
        font-weight: 500;
        transition: all 0.2s ease-in-out;
        text-shadow: 2px 2px 4px rgba(0,0,0,0.2);
        display: flex;
        width: 100%;
        
        &:hover {
          background: rgba(255,255,255,0.1);
          box-shadow: 4px 4px 60px 8px rgba(0,0,0,0.2);
        }
        
        &[type="email"],
        &[type="password"] {
          
          &:focus {
            background: rgba(255,255,255,0.1);
            box-shadow: 4px 4px 60px 8px rgba(0,0,0,0.2);
          }
        }
        
        &[type="button"] {
          margin-top: 10px;
          width: 180px;
          font-size: 1.5rem;
          
          &:hover {
            cursor: pointer;
          }
          
          &:active {
            background: rgba(255,255,255,0.2);
          }
        }
      }
      
}
.select-input{
  background: transparent;
  width: 300px;
  padding: 0 1.5rem;
  margin-bottom: 2em;
  border: none;
  border-left: 1px solid #fff;
  border-top: 1px solid #fff;
  border-radius: 5000px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  box-shadow: 4px 4px 60px rgb(0 0 0 / 20%);
  color: #000;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  text-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
  display: flex;
  width: 100%;
  min-height: 42px;

}
.payment-information-method{
    margin-top: 25px;
}
.payment-method-item{
    display: flex;
    width: 100%;
    padding: 10px 20px;
    border: 1px solid #ccc;
    border-radius: 20px;
    margin-bottom: 10px;
}
.payment-method-item-type{
    display: flex;
    align-items: center;
    margin-left: 15px;
    img{
        width: 2.8rem;
        border-radius: 10px;
        margin-right: 5px;
    }
    .payment-item-type-content{
        font-size: 1.7rem;
    }
}
.date-time-picker{
  width: 100%;
  margin-top: 10px!important;
}
.input-delivery{
  display: flex;
  flex-direction: column;
  border-top: 1px solid #ccc;
  padding: 10px 0;
  border-bottom:1px solid #ccc ;
  align-items: center;
  justify-content: center;
  width: 100%;
  h3{
    text-align: center;
  }
  .container-delivery{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .delivery-time{
      margin-right: 20px;
    }
    
  }
  
}
.payment-button{
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    align-items: center;
    button{
        padding: 10px 20px;
        border-radius: 20px;
        background-color: #000;
        color: #fff;
        font-size: 1.8rem;
    }
    a{
        color: #000;
        font-size: 1.8rem;
        font-weight: 600;
    }
}
.check-map{
  background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: 500px;
    max-width: 400px;
    margin-left: 10px;
    flex: 1;
    margin-bottom: 20px;
    .info-map{
      display: flex;
      align-items: center;
      justify-content: center;
      span{
        margin: 5px 40px;
      }
    }
}

.check-payment{
  background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: 500px;
    max-width: 400px;
    margin-left: 10px;
    flex: 1;
}
.payment-body{
  margin-top: 10px;
  border-radius: 10px;
  padding:0 10px;
  a{
      color: #000;
  }

}
.payment-item{
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  border-radius: 10px;
  justify-content: space-between;

  &:hover{
      box-shadow: rgb(153 153 153 / 60%) 0px 0px 10px 0px;
      transform: scale(1.03);
      
  }
}
.payment-inner{
  display: flex;
  align-items: center;
}
.image{
  width: 50px;
  height: 50px;
  object-fit: cover;

}
.payment-info{
  margin: 0 10px;
  text-align: left;

  & h3 {
      // display: block;
      display: -webkit-box;
      // text-overflow: ellipsis;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
  }

}
.payment-button{
  padding: 5px 10px;
  border-radius: 10px;
  margin-top: -3px;
  margin-right: 3px;
  button{
    cursor: pointer;
    &:hover{
    background-color: rgb(51, 51, 51);
      
    }
  }
}
.payment-price-qty{
  display: flex;
  align-items: center;
  justify-content: space-between;
  div{
    margin: 0 50px 0 0;
  }
}
.payment-total{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  p{
      font-size: 2rem;
      font-weight: 500;
  }
  span,div{
      font-size: 2rem;
      font-weight: 500;
  }
}
.payment-button-check{
  margin-top: 10px;
  width: 100%;
  a{
      color: #fff;
      background-color: rgb(85, 85, 85);
      padding: 10px 20px ;
      border-top-left-radius: 15px ;
      border-bottom-right-radius: 15px;
      width: 100%;
      display: flex;
      justify-content: center;
      &:hover{
          background-color: #000;
      }
  }
  button{
      color: #fff;
      background-color: rgb(85, 85, 85);
      padding: 10px 20px ;
      border-top-left-radius: 15px ;
      border-bottom-right-radius: 15px;
      width: 100%;
      display: flex;
      justify-content: center; 
  }
}
.payment-wrapper{
  overflow-y: auto;
  max-height: 70%;
}
.input-radio{
       background: transparent; 
     width: 1.5rem; 
     padding: 0; 
     margin-bottom: 0; 
     border: none; 
     border-left: 1px solid rgba(255, 255, 255, 0.3); 
     border-top: 1px solid rgba(255, 255, 255, 0.3); 
     border-radius: 5000px; 
    -webkit-backdrop-filter: blur(5px);
     backdrop-filter: blur(5px); 
     box-shadow: 4px 4px 60px rgb(0 0 0 / 20%); 
     color: #000; 
     font-family: Montserrat, sans-serif; 
     font-weight: 500; 
     transition: all 0.2s ease-in-out; 
     text-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
     &:hover{
      background: #fff;
      box-shadow: 4px 4px 60px 8px #fff;
     }
}
    
@media (max-width:728px){
  .inner{
    padding:  0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding-bottom: 100px;
  }
  .payment-cart{
    display: none;
  }
  .payment-information{
    background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0,0,0,0.2);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    width: 100%;
    min-width: fit-content;
    max-width: fit-content;
   
   }
}
.payment-check-policy{
  display: flex;
  align-items: center;
  margin: 50px 0 20px 0;
  p{
    font-weight: 500;
    color: rgb(57, 57, 57);
    opacity: 0.9;
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 20px;
    text-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
  }
  .payment-check-input{
      background: transparent;
      width: 40px;
      padding: 0em;
      margin-bottom: 0em;
      border: none;
      border-left: none;
      border-top: none;
      border-radius: 50%;
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
      box-shadow: 4px 4px 60px rgb(0 0 0 / 20%);
      color: #000;
      font-family: Montserrat, sans-serif;
      font-weight: 500;
      transition: all 0.2s ease-in-out;
      text-shadow: 2px 2px 4px rgb(0 0 0 / 20%);
  }
  span{
    color: #000;
    text-decoration: underline;
    margin-left: 5px;
    cursor: pointer;
    &:hover{
      opacity: 0.8;
    }
  }
}
.policy{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  .policy-header{

  }
  .policy-body{
      overflow-y: auto;
      max-height: 80%;
      margin-top: 20px;
  }
}