.order-item{
    display: flex;
    align-items: center;
   
}
.order-inner-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.order-list{
    background: rgba(255, 255, 255, 0.3);
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid rgba(255, 255, 255, 0.3);
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgb(0 0 0 / 20%);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    max-width: 1000px;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    animation: slideUp ease-in 0.8s;
    
}
.order-list-none-product{
    background: rgba(255, 255, 255, 0.3);
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid rgba(255, 255, 255, 0.3);
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgb(0 0 0 / 20%);
    text-align: center;
    position: relative;
    transition: all 0.2s ease-in-out;
    max-width: 900px;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2{
        font-size: 2.5rem;
        color: rgb(114, 114, 114);
    }
    a{
        padding: 20px;
        border-radius: 10px;
        background-color: #000;
        color: #fff;
        max-width: 200px;
        margin: 20px 0;
        &:hover{
            background-color: rgb(34, 34, 34);
        }
    }
}
.body-product-order-item{
    margin: 10px 0;
    
}
.order-info{
    flex: 1;
}
.table{
    
        --bs-table-bg: transparent;
        --bs-table-accent-bg: transparent;
        --bs-table-striped-color: #212529;
        --bs-table-striped-bg: rgba(0, 0, 0, 0.05);
        --bs-table-active-color: #212529;
        --bs-table-active-bg: rgba(0, 0, 0, 0.1);
        --bs-table-hover-color: #212529;
        --bs-table-hover-bg: rgba(0, 0, 0, 0.075);
        width: 100%;
        margin-bottom: 1rem;
        color: #212529;
        vertical-align: top;
        border-color: #dee2e6;
        caption-side: bottom;
        border-collapse: collapse;
        border-radius: 1em;
        overflow: hidden;
    
}
.table>thead {
    vertical-align: bottom;

}
thead{

    justify-content: space-between;
    display:table-header-group;
}
tbody{
    display: table-row-group;
}

.alert-danger {
    color: #842029;
    background-color: #f8d7da;
    border-color: #f5c2c7;
}
.alert-success {
    color: #fff;
    background-color: #83fbab;
    border-color: #f5c2c7;
}
tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
    width: 100%;
    margin: auto;
    text-align: center;
   
}
.table {
    text-align: center;
    display: table;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    
    border-collapse: separate;
    border-radius: 1em;
    border-spacing: 0 1.5rem;
  
}

tbody{
    border-collapse: collapse;
    border-radius: 1em;
    overflow: hidden;
}

td{
    padding: 10px 15px;
    border-bottom: 2px solid white; 
    
    
}

.table>:not(caption)>*>* {
    padding: 0.5rem 0.5rem;
    background-color: var(--bs-table-bg);
    border-bottom-width: 1px;
    box-shadow: inset 0 0 0 9999px var(--bs-table-accent-bg);
}
tbody, td, tfoot, th, thead, tr {
    border-color: inherit;
    border-style: solid;
    border-width: 0;
}
th {
    text-align: inherit;
    text-align: -webkit-match-parent;
}
*, ::after, ::before {
    box-sizing: border-box;
}

th {
    display: table-cell;
    vertical-align: inherit;
    font-weight: bold;
    text-align: -internal-center;
}
thead {
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
}

.body-product-order-item {
    border: 2px ;
    border-radius: 10px;

}
@keyframes slideUp {
    from{
        opacity: 0;
        transform: translateY(20%);
    }to{
        opacity: 1;
        transform: translateY(0);
    }
}
@media (max-width: 777px) {
    .wrapper{
       height: 120vh;

    }
    
    .order-list{
        background: rgba(255, 255, 255, 0.3);
        padding: 20px;
        
        height: auto;
        border-radius: 10px;
        border-left: 1px solid rgba(255, 255, 255, 0.3);
        border-top: 1px solid rgba(255, 255, 255, 0.3);
        -webkit-backdrop-filter: blur(6px);
        backdrop-filter: blur(6px);
        box-shadow: 20px 20px 40px -6px rgb(0 0 0 / 20%);
        text-align: center;
        position: relative;
        transition: all 0.2s ease-in-out;
        max-width: 1000px;
        display: flex;
        margin: auto;
        flex-wrap: wrap;
        animation: slideUp ease-in 0.8s;
        
    }
    .table>:not(caption)>*>* {
        padding: 0.5rem 0.5rem;
        background-color: var(--bs-table-bg);
        border-bottom-width: 1px;
        box-shadow: inset 0 0 0 9999px var(--bs-table-accent-bg);
        
    }
   
    .id-table{display: none;}
    .content-id-table{display: none;}
}