.wrapper{
    padding-bottom: 100px;
}
.order-bill-logo {
    width: 150px;
}
.button-download {
    display: flex;
    width: 100%;
    padding: 0 150px;

    justify-content: flex-end;
    .download {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        background-color: #000;
        color: #fff;
        padding: 10px 10px;
        border-radius: 50%;
        &:hover{
            opacity: 0.8;
        }
    }

}
.button-download-wrapper {
    background-color: #fff;
    max-width: 200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    button {
        background: transparent;
        padding: 10px 20px;
        cursor: pointer;
        &:hover {
            color: rgb(113, 113, 113);
        }
    }
    .button-pdf {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
       background: #000;
       margin-bottom: 10px;
        padding: 10px 10px;
        border-radius: 50%;
        &:hover{
            opacity: 0.8;
        }
    }
    .button-image {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        background: #000;
        color: #fff;
        padding: 10px 10px;
        border-radius: 50%;
        &:hover{
            opacity: 0.8;
        }
    }
}
.order-bill {
    padding: 50px 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #fff;
    padding: 3em;
    height: auto;
    border-radius: 10px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    backdrop-filter: blur(6px);
    box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.2);

    position: relative;
    transition: all 0.2s ease-in-out;
    min-width: 300px;
    margin: auto;
    max-width: 688px;
    .order-bill-header {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        width: 100%;
        border-bottom: 1px solid #ccc;
        margin-bottom: 20px;
        .order-bill-title {
            font-size: 40px;
            font-weight: 600;
        }
        .order-bill-address {
            font-size: 20px;
        }
        h3 {
            font-size: 30px;
        }
    }
    .order-bill-body {
        font-size: 20px;
        width: 100%;
        .order-bill-id-and-time {
            display: flex;

            justify-content: space-between;
        }
    }
    .order-bill-footer {
        border-top: 1px solid #ccc;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
}
.extra-option{
    display: flex;
    align-items: center;
    justify-content:space-between;
    width:100%;
    .option{
        font-size: 1.5rem;
    }
}
.topping{
    font-size: 1.5rem; 
    .topping-list{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: 80%;
        .topping-item{
            margin-right: 5px;
        }
    }
}
.button-download{
    position: fixed;
    cursor: pointer;
    display: inline-block;
    margin-top: 20px;
    margin-left: 25px;
}
@media (max-width:770px) {
    .button-download {display: none;}
}