.home {
    background: url('../../assets/apply-background.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    min-height: 100vh;
    h3{
        padding: 1rem 0;
        font-size: 5rem;
        color: #fff;
    }
    span{
        font-size: 2.5rem;
        color: #fff;
    }
}
.contact{
    margin-bottom: 30px ;
}
.container{
    display: flex;
    align-items: center;
    min-height: 100vh;
    padding: 0 50px;
}
.layout{
    width: 50%;
    
}
.non-layout{
    width: 50%;
}
.container{
    margin: 0 auto ;
    padding: 0 15px;
    max-width: 1140px;
    display: flex;
}
.header-form{
    font-size: 2.2rem;
    font-weight: 500;
    margin-bottom: 5px;
}
.form{
    width: 50%;
    margin-top: 30px;
}
.non-form{
    width: 50%;
}
.input-form{
    height: 40px;
    width: 100%;
    position: relative;
    margin-bottom: 30px;
    input{
        height: 100%;
        width: 100%;
        border: none;
        font-size: 17px;
        border-bottom: 2px solid silver;
    }
    .underline{
        position: absolute;
        height: 2px;
        width: 100%;
        bottom: 0;
    }
    label{
        position: absolute;
        bottom: 10px;
        left: 0;
        font-size: 15px;
        color: grey;
        pointer-events: none;
        transition: all 0.3s ease;
    }
}
.input-form input:focus ~ label,
.input-form input:valid ~ label{
  transform: translateY(-20px);
  font-size: 15px;
  color: var(--black);
}
 .input-form label{
  position: absolute;
  bottom: 10px;
  left: 0;
  font-size: 15px;
  color: grey;
  pointer-events: none;
  transition: all 0.3s ease;
}
.input-form .underline{
  position: absolute;
  height: 2px;
  width: 100%;
  bottom: 0;
}
.input-form .underline:before{
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: var(--black);
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.3s ease;
}
.input-form input:focus ~ .underline:before,
.input-form input:valid ~ .underline:before{
  transform: scaleX(1);
}
.button {
    position: relative;
    display: inline-block;
    width: 150px;
    height: 50px;
    background: var(--black);
    font: inherit;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--white);
    border: none;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    &:focus{
        animation: morph 1.25s forwards;
        
    }
    &:hover{
        opacity: 0.8;
    }
}

.button > * {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}
.svg-inline--fa .plane {
    display: none;
}
.sent{
    
}

.sent,
.button:focus span {
    opacity: 0;
    visibility: hidden;
}
.button:focus .sent {
    animation: reveal 1s 1.25s forwards;

}
.button:focus .svg-inline--fa.plane{
    display: block;
    animation: takeOff 1s forwards;
    z-index: 1;
}

@keyframes reveal {
    100% {
        opacity: 1;
        visibility: visible;
    }
}
@keyframes morph {
    10%,
    80% {
        width: 50px;
        border-radius: 50%;
        background: #f3f3f3;
        color: #3c2e9e;
    }
    100% {
        background: var(--black);
        color: #f3f3f3;
    }
}
@keyframes takeOff {
    5%,
    15%,
    25%,
    35%,
    45% {
        transform: translate(-47%, -50%);
    }
    10%,
    20%,
    30%,
    40%,
    50% {
        transform: translate(-53%, -50%);
    }
    90% {
        transform: translate(100px, -100px);
    }
    100% {
        transform: translate(100px, -100px);
    }
}