:root {
    --clr: #222327;
    --reqBlue: #1ae7e0;
}
.wrapper {
    height: 90px;
}
.inner {
    z-index: 999;
    position: absolute;
    height: 90px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 0 150px;
    margin-right: auto;
    margin-left: auto;
    position: fixed;
    background-color: #fff;
}

.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.nav-item {
    font-size: 2.1rem;
    margin-right: 2rem;
    font-weight: 600;
    color: rgb(113, 114, 114);
    display: inline-block;
    position: relative;
    letter-spacing: 1px;
    &:hover {
        color: #000;
    }
}
.nav-item::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: rgb(98, 98, 98);
    transform: scaleX(0);
    transition: tranform 0.25s;
}

.nav-item:hover::after {
    transform: scaleX(1);
    animation: navHover ease-in 0.2s;
}

.nav-item::after {
    bottom: -3px;
    transform-origin: right;
}

@keyframes navHover {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}
.logo-img {
    height: 100px;
    width: 100px;
    background: url("../../../assets/logo/tachnen/greylogo.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-size: 2.5rem;
}
.header-icon {
    font-size: 2.5rem;
    font-weight: 600;
    color: rgb(113, 114, 114);
    margin-left: 2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    a {
        color: #000;
    }
    form {
        background: transparent;
        padding: 0;

        border-radius: none;
        border-left: none;
        border-top: none;
        -webkit-backdrop-filter: none;
        backdrop-filter: none;
        box-shadow: none;
        text-align: center;
        position: relative;
        transition: none;
        min-width: fit-content;
        max-width: fit-content;
        input {
            margin: 0;
            padding: none;
            margin-bottom: none;
            border: none;
            border-left: none;
            border-top: none;
            border-radius: none;
            -webkit-backdrop-filter: none;
            backdrop-filter: none;
            box-shadow: none;
            color: none;
            font-family: Montserrat, sans-serif;
            font-weight: none;
            transition: none;
            text-shadow: none;
            &:hover {
                background: transparent;
                box-shadow: none;
            }
        }
    }
    .search-box {
        width: fit-content;
        height: fit-content;
        position: relative;
        ///
    }
    .input-search {
        height: 50px;
        width: 50px;
        border-style: none;
        padding: 10px;
        font-size: 20px;
        letter-spacing: 2px;
        outline: none;
        border-radius: 25px;
        transition: all 0.5s ease-in-out;
        background-color: transparent;
        padding-right: 40px;
        color: #000;
        z-index: 999;
    }
    .search-icon {
        color: #000;
        font-size: 25px;
        font-weight: 600;
        z-index: 999;
    }
    .input-search::placeholder {
        color: #000;
        font-size: 15px;
        letter-spacing: 2px;
        font-weight: 100;
    }
    .btn-search {
        width: 50px;
        height: 100%;
        border-style: none;
        font-size: 20px;
        font-weight: bold;
        outline: none;
        cursor: pointer;
        border-radius: 50%;
        position: absolute;
        right: 0px;
        color: #ffffff;
        background-color: transparent;
        pointer-events: painted;
        z-index: 9;
    }
    .btn-search:focus ~ .input-search {
        width: 80%;
        border-radius: 0px;
        background-color: transparent;
        border-bottom: 1px solid #000;
        transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
    }
    .input-search:focus {
        width: 80%;
        border-radius: 0px;
        background-color: transparent;
        border-bottom: 1px solid #000;
        transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
    }
}
.icon {
    padding: 0 10px;
    &:hover {
        color: #000;
        transform: scale(1.03);
    }
}
.cart-icon {
    color: rgb(113, 114, 114);
    position: relative;
    span {
        position: absolute;
        border-radius: 50%;
        background-color: #000;
        color: #fff;
        padding: 5px 10px;
        font-size: 1rem;
        right: -5px;
        top: -10px;
    }
    .new-notification {
        position: absolute;
        width: 100px;
        text-align: center;
        top: 40px;
        right: -20px;
        border-radius: 10px;
        &::before {
            position: absolute;
            content: "";
            cursor: pointer;
            right: 32px;
            top: -20px;
            border-color: transparent transparent #000 transparent;
            border-width: 10px 10px;
            border-style: solid;
        }
    }
}
.user-navbar {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    min-width: 120px;
    border-radius: 10px;
    a {
        font-size: 1.8rem;
        font-weight: 600;
        color: #000;
        padding: 15px 25px;
        border-radius: 10px;

        &:hover {
            background-color: #ccc;
        }
    }
}
.button-user {
    margin-top: -5px;
    a {
        color: #000;
        font-size: 2rem;
        margin: 0 5px;

        padding: 10px 15px;
        border-radius: 10px;
        min-width: 120px;
        max-width: 120px;
    }
    :first-child {
        &:hover {
            color: rgb(73, 73, 73);
        }
    }
    :last-child {
        background-color: #000;
        color: #fff;
        cursor: pointer;
        &:hover {
            background-color: rgb(61, 61, 61);
        }
    }
}
.navigation {
    display: none;
}
.modal-menu {
    display: none;
}
.layout-option {
    display: none;
}
.active-layout-in {
    display: block;
    animation: activeInLayout 0.8s ease-in;
}
@keyframes activeInLayout {
    0% {
        opacity: 0;
        transform: translateY(80%);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
.active-in {
    display: block;
    animation: activeIn 0.7s ease-in;
}
.active-out {
    opacity: 0;
    display: none;
    transform: translateY(-60%);
    animation: activeOut 1s ease-out;
}
@keyframes activeIn {
    0% {
        opacity: 0;
        transform: translateY(60%);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes activeOut {
    0% {
        transform: translateY(0);
    }

    100% {
        opacity: 0;
        transform: translateY(60%);
    }
}
.layout-warpper-cart {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 550;
    background-color: rgba(0, 0, 0, 0.5);
}
//bottom sheet
.bottom-sheet-overlay {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
}
.bottom-overlay-active {
    display: block;
    z-index: 9;
    animation: overlayAnimated 0.3s linear;
}
.bottom-overlay-unactive {
    z-index: -9;
    animation: overlayAnimatedOut 0.8s linear;
}

@keyframes overlayAnimated {
    0% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}
@keyframes overlayAnimatedOut {
    100% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    0% {
        opacity: 0;
    }
}
.bottom-sheet {
    background-color: #fff;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    bottom: 0;
    cursor: grab;
    height: auto;
    position: fixed;

    z-index: 100;
    width: 100%;
    .layout-cart-body {
        z-index: 100;
        margin-top: 25px;
        height: calc(100% - 113px);
        .cart-list {
            z-index: 100;
            max-height: 400px;
            overflow-y: scroll;
            scroll-behavior: smooth;
            padding: 10px 20px;
            .cart-item {
                z-index: 100;
                padding: 10px;
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;

                .cart-item-img {
                    z-index: 100;
                    width: 60px;
                    border-radius: 15px;
                    object-fit: contain;
                    margin-right: 5px;
                }
                .cart-item-inf {
                    display: flex;
                    z-index: 100;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;
                    .cart-item-name {
                        font-size: 25px;
                        z-index: 100;
                    }
                    .cart-item-price {
                        font-size: 20px;
                        z-index: 100;
                        max-width: 100px;
                    }
                }
                .cart-trash-button {
                    font-size: 20px;
                    padding: 10px;
                    border-radius: 10px;
                    margin-left: 5px;
                    z-index: 100;
                    color: #000;
                    cursor: pointer;
                }
            }
        }
    }
    .layout-cart-footer {
        background-color: #fff;

        z-index: 100;
        bottom: 0;
        width: 100%;
        .layout-cart-footer-total {
            display: flex;
            align-items: center;
            justify-content: space-between;
            z-index: 100;
            padding: 10px 20px;
            h3 {
                font-size: 20px;
            }
            span {
                font-size: 20px;
                max-width: 100px;
            }
        }
    }
    .layout-cart-footer-button {
        width: 100%;
        button {
            display: flex;
            z-index: 100;
            margin: auto;
            justify-content: center;
            align-items: center;
            text-align: center;
            font-size: 20px;
            padding: 20px 100px;
            width: 90%;
            border-radius: 20px;
            background-color: #000;
            color: #fff;
            cursor: pointer;
            &:hover {
                opacity: 0.8;
            }
        }
    }
}

.bottom-sheet-bar {
    background: #ccc;
    border-radius: 2px;
    height: 4px;
    left: 50%;
    position: absolute;
    top: 8px;
    transform: translateX(-50%);
    width: 56px;
}

.layout-cart {
    position: fixed;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    z-index: 555;
    .layout-cart-bar {
        position: absolute;
        width: 50%;
        cursor: grab;
        height: 6px;
        top: 15px;
        text-align: center;
        display: flex;
        margin: auto;
        justify-content: center;
        right: 95px;
        border: 2px solid #000;
        border-radius: 20px;
        background: #000;
    }
}

