.layout {
    position: fixed;
    top: 0;
    bottom: 0;
    display: none;
    left: 0;
    right: 0;
    background-color: transparent;
    z-index: 0;
}

.layout-option {
    position: fixed;
    top: 0;
    bottom: 0;
    display: none;
    height: 100%;
    left: 0;
    right: 0;
    background-color: #000;
    z-index: 2;
    animation: slideIn 0.2s ease-in;

    z-index: 10000;

    .active-option-layout {
        display: block;
        animation: slideIn 1s ease-in;
    }
    .layout-option-icon {
        font-size: 2.5rem;
        margin-right: 20px;
        padding: 20px;
        font-weight: 400;
        cursor: pointer;
        display: block;
        float: right;
        color: #fff;
    }

    .layout-option-inner {
        padding: 0 50px;
        margin-top: 50px;

        .layout-option-list {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            a {
                color: #fff;
                letter-spacing: 0.5rem;
                font-size: 2rem;
                font-weight: 400;
                margin: 20px 0;
                position: relative;
                transition: all 0.25s;
                &:hover {
                    transform: scale(1.05);
                }
            }
        }
    }
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slideOut {
    from {
        transform: translateX(0);
        opacity: 1;
    }
    to {
        transform: translateX(100%);
        opacity: 0;
    }
}
.active-layout {
    display: block;
}
