.wrapper {
    padding: 0 150px;
    width: 100%;
}
.review-avatar-overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 999;
    .review-avatar-inner{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .review-image{
        height: 50%;
        object-fit: contain;
        border-radius: 20px;
        width: 50%;
    }
}
.loading-overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    .load{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);
      /*change these sizes to fit into your project*/
      width:100px;
      height:100px;
    }
    .load hr{border:0;margin:0;width:40%;height:40%;position:absolute;border-radius:50%;animation:spin 2s ease infinite}
    
    .load :first-child{background:#000;animation-delay:-1.5s}
    .load :nth-child(2){background:rgb(131, 131, 131);animation-delay:-1s}
    .load :nth-child(3){background:rgb(78, 77, 77);animation-delay:-0.5s}
    .load :last-child{background:rgb(255, 255, 255)}
    
    @keyframes spin{
      0%,100%{transform:translate(0)}
      25%{transform:translate(160%)}
      50%{transform:translate(160%, 160%)}
      75%{transform:translate(0, 160%)}
    }
  
  }
.dropzone-overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    display: none;
    justify-content: center;
    align-items: center;
    margin: auto;
    bottom: 0;
    .dropzone-container {
        width: 600px;
        height: 400px;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
        flex-direction: column;
        border-radius: 20px;
    }
    .dropzone-header {
        position: relative;
        .close-button {
            background: #fff;
            position: absolute;
            top: -40px;
            right: -280px;
        }
        .close-icon {
            font-size: 2.5rem;
            background: #fff;
        }
    }
    .dropArea {
        display: grid;
        place-items: center;
        background-color: transparent;
        border: 1px dashed #ccc !important;
        width: 540px;
        height: 300px;
        cursor: pointer;
        background-color: rgb(233, 233, 233);
        border-radius: 20px;
    }
}
.drop-zone-active{
    display: flex;
}
.inner {
    text-align: center;
}
.avatar-user{
    position: relative;
    display: inline-block;
    border-radius: 100%;
    &:hover .overlay-avatar{
        opacity: 1;
    }
   .overlay-avatar{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    height: 99%;
    cursor: pointer;
    transition: all .25s;
    opacity: 0;
    align-items: center;
    span{
        color: #fff;
    }
   }
}
.header-image {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    object-fit: cover;
    position: relative;
   
   
}


//bottom sheet
.bottom-sheet-overlay{
    display: none;
    position: fixed;
    top: 0 ;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
}
.bottom-overlay-active{
    display: block;
    z-index: 9;
    animation: overlayAnimated 0.3s linear;
}
.bottom-overlay-unactive{
   
    z-index: -9;
    animation: overlayAnimatedOut .8s linear;

}

@keyframes overlayAnimated {
    0% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}
@keyframes overlayAnimatedOut {
    100% {
        opacity: 1;
    }
    50%{
        opacity: 0.5;
    }
    0% {
        opacity: 0;
    }
}
.bottom-sheet {
    background-color: #fff;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    bottom: 0;
    cursor: grab;
    height: 248px;
    position: fixed;

    z-index: 100;
    width: 100%;
    .layout-cart-body {
      
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 20px auto;
        width: 80%;
        .button-avatar{
            font-size: 2.5rem;
            margin: 10px 0 ;
            background: transparent;
            padding: 10px 20px;
            width: 100%;
            border-bottom: 1px solid #ccc;
            cursor: pointer;
            &:hover{
                opacity: 0.8;
            }
       }
    }
   

}

.bottom-sheet-bar {
    background: #ccc;
    border-radius: 2px;
    height: 4px;
    left: 50%;
    position: absolute;
    top: 8px;
    transform: translateX(-50%);
    width: 56px;
}
.header-img-button{
    position: absolute;
    top: 236px;
    left: 830px;
    background: transparent;
    .header-img-icon{
        font-size: 2.5rem;
        background: transparent;
    }
}
.button-user {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 20px 0;
    button {
        color: rgb(130, 130, 130);
        cursor: pointer;
        font-size: 2rem;
        background-color: transparent;
        &:hover {
            color: #000;
        }
    }
}
.active {
    color: #000 !important;
    padding: 0 10px;
    border-bottom: 2px solid #ccc;
}
.header-user-name {
    margin: 20px 0;
}
.form-input {
    margin: 5px 15px;
}
.body-profile {
    width: 80%;
    text-align: center;
    display: flex;
    margin: auto;
    .button-change {
        padding: 20px 30px;
        border-radius: 20px;
        cursor: pointer;
        &:hover {
            color: #fff;
            background-color: #000;
        }
    }
}

.form-input-center {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.password-input-btn {
    position: relative;
    input {
        width: 300px;
    }
    button {
        position: absolute;
        top: 15px;
        right: 20px;
        z-index: 1;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        font-size: 1.5rem;
        border-radius: 50%;
        cursor: pointer;
        background: #ffffff;
        color: #787878;
    }
}
.tab-content > .active {
    display: block;
}
.tab-content > .tab-pane {
    display: none;
}
.fade {
    transition: opacity 0.15s linear;
}
@media (max-width: 777px) {
    .wrapper {
        padding: 0 0;
        width: 100%;
    }
}
